import { ComponentChild } from 'preact';
import { useState, useRef, useEffect } from 'preact/hooks';
import { cssProps } from '@oracle/oraclejet-cssprops';
import { useId } from '../UNSAFE_useId';
import { Floating, Offset, Placement, Coords, Rect } from '../../UNSAFE_Floating';
import { Layer } from '../../UNSAFE_Layer';
import { useHover } from '../UNSAFE_useHover';
import { useFocus } from '../UNSAFE_useFocus';
import { useTouch } from '../UNSAFE_useTouch';
import { mergeProps } from '../../utils/UNSAFE_mergeProps';
import { stringLiteralArray } from '../../utils/UNSAFE_arrayUtils';
import { classNames } from '../../utils/UNSAFE_classNames';

export const positions = stringLiteralArray([
  'start',
  'top-start',
  'top',
  'top-end',
  'end',
  'bottom-end',
  'bottom',
  'bottom-start'
]);
type Position = typeof positions[number];

export const anchorTos = stringLiteralArray(['element', 'pointer']);
type AnchorTo = typeof anchorTos[number];

type AnchorModel = { x: AnchorTo; y: AnchorTo };

type Props = {
  text?: string;
  variant?: 'tooltip' | 'datatip';
  isOpen: boolean;
  position?: Position;
  isDisabled?: boolean;
  anchor?: AnchorModel;
  offset?: { mainAxis?: number; crossAxis?: number };
  onToggle?: (details: { value: boolean }) => void;
};

const styles = {
  wrapper: cssProps`${{
    display: 'flex',
    alignItems: 'flex-end',
    overflowY: 'hidden'
  }}`,
  base: cssProps`${{
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-tooltip-text-color)',
    backgroundColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-tooltip-bg-color)',
    paddingTop: 'var(--oj-c-PRIVATE-DO-NOT-USE-tooltip-padding-vertical)',
    paddingLeft: 'var(--oj-c-PRIVATE-DO-NOT-USE-tooltip-padding-horizontal)',
    paddingRight: 'var(--oj-c-PRIVATE-DO-NOT-USE-tooltip-padding-horizontal)',
    paddingBottom: 'var(--oj-c-PRIVATE-DO-NOT-USE-tooltip-padding-vertical)',
    borderRadius: 'var(--oj-c-PRIVATE-DO-NOT-USE-tooltip-border-radius)',
    borderColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-tooltip-border-color)',
    fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-tooltip-font-size)',
    borderWidth: 'var(--oj-c-PRIVATE-DO-NOT-USE-tooltip-border-width)',
    borderStyle: 'solid'
  }}`,
  datatip: cssProps`${{
    '--oj-c-PRIVATE-DO-NOT-USE-tooltip-bg-color':
      'rgb(var(--oj-c-PRIVATE-DO-NOT-USE-palette-neutral-rgb-0))',
    '--oj-c-PRIVATE-DO-NOT-USE-tooltip-text-color':
      'rgb(var(--oj-c-PRIVATE-DO-NOT-USE-palette-neutral-rgb-190))',
    '--oj-c-PRIVATE-DO-NOT-USE-tooltip-padding-vertical':
      'var(--oj-c-PRIVATE-DO-NOT-USE-core-spacing-2x)',
    '--oj-c-PRIVATE-DO-NOT-USE-tooltip-border-radius':
      'var(--oj-c-PRIVATE-DO-NOT-USE-core-border-radius-md)',
    '--oj-c-PRIVATE-DO-NOT-USE-tooltip-border-color':
      'rgba(var(--oj-c-PRIVATE-DO-NOT-USE-palette-neutral-rgb-190), 0.65)',
    '--oj-c-PRIVATE-DO-NOT-USE-tooltip-font-size':
      'var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-xs-font-size)'
  }}`
};

export const useTooltipControlled = ({
  text,
  isOpen = false,
  variant = 'tooltip',
  position = 'bottom',
  isDisabled = false,
  anchor = { x: 'element', y: 'element' },
  offset = { mainAxis: 0, crossAxis: 0 },
  onToggle
}: Props): {
  tooltipContent: ComponentChild;
  tooltipProps: Record<string, any>;
} => {
  // TODO - consider using useReducer to potentially simplify the implementation
  // as part of a future improvement issue.
  const [state, setState] = useState<
    | 'hidden'
    | 'mountTimeout'
    | 'mountInitialize'
    | 'mounting'
    | 'mounted'
    | 'unmountTimeout'
    | 'unmounting'
    | 'unmounted'
  >(isOpen ? 'mounted' : 'hidden');
  const isDatatip = variant === 'datatip';
  const { hoverProps, isHover } = useHover({ isDisabled });
  const { focusProps, isFocus } = useFocus({ isDisabled });
  const { touchProps, isTouch } = useTouch({ isDisabled }); // TODO replace by useLongPress actionhook
  const { hoverProps: popoverHoverProps, isHover: popoverIsHover } = useHover({ isDisabled });
  const { touchProps: popoverTouchProps, isTouch: popoverIsTouch } = useTouch({ isDisabled });

  const uniqueID = useId();
  const isInitialRender = useRef(true);
  const popoverRef = useRef<HTMLDivElement>(null);
  const targetRef = useRef<HTMLElement | null>(null);
  const coordsRef = useRef<Coords>({ x: 0, y: 0 });
  const usedRef = anchor.x === 'element' && anchor.y === 'element' ? targetRef : coordsRef;
  const delay = isDatatip ? 0 : 250;

  const handleToggle = (isOpen: boolean) => {
    setState(isOpen ? 'mountTimeout' : 'unmountTimeout');
  };

  useEffect(() => {
    if (isInitialRender.current) {
      isInitialRender.current = false;
      return;
    }
    // No state updates in case focus or touch is already present
    if (isFocus || isTouch) {
      return;
    }
    handleToggle(isHover);
  }, [isHover]);

  useEffect(() => {
    if (isInitialRender.current) {
      isInitialRender.current = false;
      return;
    }
    // No state updates in case hover or touch is already present
    if (isHover || isTouch) {
      return;
    }
    handleToggle(isFocus);
  }, [isFocus]);

  useEffect(() => {
    if (isInitialRender.current) {
      isInitialRender.current = false;
      return;
    }
    // No state updates in case hover or focus is already present
    if (isHover || isFocus) {
      return;
    }
    handleToggle(isTouch);
  }, [isTouch]);

  useEffect(() => {
    if (isInitialRender.current) {
      isInitialRender.current = false;
      return;
    }
    if (['mounted', 'unmountTimeout'].includes(state)) {
      setState(popoverIsHover || popoverIsTouch ? 'mounted' : 'unmountTimeout');
    }
  }, [popoverIsHover, popoverIsTouch]);

  useEffect(() => {
    let timeout: ReturnType<typeof setTimeout>;
    switch (state) {
      case 'mountTimeout':
        timeout = setTimeout(() => setState('mountInitialize'), delay);
        break;
      case 'mountInitialize':
        setState('mounting');
        onToggle?.({ value: true });
        break;
      case 'mounting':
        setState('mounted');
        break;
      case 'unmountTimeout':
        timeout = setTimeout(() => setState('unmounting'), delay);
        break;
      case 'unmounting':
        setState('unmounted');
        onToggle?.({ value: false });
        break;
    }
    return () => clearTimeout(timeout);
  }, [state]);

  let actionableProps;
  if (isDisabled) {
    actionableProps = {};
  } else {
    const anchorInitProps = {
      'aria-describedby': uniqueID,
      onMouseEnter: (event: MouseEvent) => {
        if (event.eventPhase === Event.AT_TARGET) {
          targetRef.current = event.target as HTMLElement;
        }
      },
      ...((anchor.x === 'pointer' || anchor.y === 'pointer') && {
        onMouseMove: (event: MouseEvent) => {
          if (event.eventPhase === Event.AT_TARGET) {
            handleMouseMove(event);
          }
        }
      }),
      onFocus: (event: FocusEvent) => {
        if (event.eventPhase === Event.AT_TARGET) {
          targetRef.current = event.target as HTMLElement;
        }
      }
    };
    actionableProps = mergeProps(hoverProps, focusProps, touchProps, anchorInitProps);
  }

  // No need to process tooltip if disabled or no text is provided
  // Include actionableProps to detect mouseenter in components
  // hovered state won't update unless actionableProps are already present in the component when mouse enters
  if (isDisabled || !text) {
    return {
      tooltipContent: null,
      tooltipProps: actionableProps
    };
  }

  // TODO JET-51708 useFloating.ts deepEqual compares function using toString method.
  // We want it to evaluate false on each rerender so that we get the updated offset value.
  const offsetFunctionLeft = ({ floating }: { floating: Rect }) => ({
    mainAxis: offset?.mainAxis,
    crossAxis: -floating.width - (offset.crossAxis || 0)
  });
  const offsetFunctionRight = ({ floating }: { floating: Rect }) => ({
    mainAxis: offset?.mainAxis,
    crossAxis: floating.width + (offset.crossAxis || 0)
  });

  const POSITIONS: {
    [K in Position]: { placement: Placement; offsetValue: Offset };
  } = {
    top: {
      placement: 'top',
      offsetValue: offset
    },
    'top-end': {
      placement: 'top-end',
      offsetValue: offsetFunctionRight
    },
    end: {
      placement: 'right',
      offsetValue: offset
    },
    'bottom-end': {
      placement: 'bottom-end',
      offsetValue: offsetFunctionRight
    },
    bottom: {
      placement: 'bottom',
      offsetValue: offset
    },
    'bottom-start': {
      placement: 'bottom-start',
      offsetValue: offsetFunctionLeft
    },
    start: {
      placement: 'left',
      offsetValue: offset
    },
    'top-start': {
      placement: 'top-start',
      offsetValue: offsetFunctionLeft
    }
  };
  const placements = POSITIONS[position];

  const isHidden = state === 'hidden' || state === 'mountTimeout';
  const popoverNode = popoverRef.current;
  const transitionStyles = {
    popoverOpacity: ['mounted', 'unmountTimeout'].includes(state)
      ? isDatatip
        ? '100%'
        : '95%'
      : '0%',
    wrapperHeight: !isHidden && popoverNode ? `${popoverNode?.offsetHeight}px` : 'auto', // offsetHeight to include datatip border
    popoverHeight:
      state === 'mountInitialize'
        ? 'none'
        : ['mounted', 'unmountTimeout'].includes(state) && popoverNode
        ? `${popoverNode?.offsetHeight}px`
        : '0'
  };

  let mouseMoveTimeout: ReturnType<typeof setTimeout>;

  const handleMouseMove = (event: MouseEvent) => {
    clearTimeout(mouseMoveTimeout);
    if (anchor.x === 'pointer' && anchor.y === 'pointer') {
      // Display tooltip at the mouse position if both anchors are set to pointer
      coordsRef.current = { x: event.clientX, y: event.clientY };
      setState(state === 'mounted' ? 'mounting' : 'mounted');
    } else if (anchor.x === 'pointer') {
      // Tooltip maintains static offset from top side of the window
      // if X anchor is set to 'pointer'
      mouseMoveTimeout = setTimeout(() => {
        const childrenY = targetRef?.current?.offsetHeight || 0;
        const childrenTop = targetRef?.current?.offsetTop || 0;
        const distance = childrenTop + childrenY;
        coordsRef.current = { x: event.clientX, y: distance };
        onToggle?.({ value: true });
      }, delay);
    } else if (anchor.y === 'pointer') {
      // Tooltip maintains static offset from left side of window
      // if Y anchor is set to 'pointer'
      mouseMoveTimeout = setTimeout(() => {
        const childrenX = targetRef?.current?.offsetWidth || 0;
        const childrenLeft = targetRef?.current?.offsetLeft || 0;
        const distance = childrenLeft + childrenX;
        coordsRef.current = { x: distance, y: event.clientY };
        onToggle?.({ value: true });
      }, delay);
    }
  };

  const handleTransitionEnd = (_: TransitionEvent) => {
    if (state === 'mounting') {
      setState('mounted');
    }
    if (state === 'unmounted') {
      setState('hidden');
    }
  };

  const renderContent = (
    <Layer>
      <Floating
        anchorRef={usedRef}
        placement={placements.placement}
        offsetValue={placements.offsetValue}>
        <div
          id={uniqueID}
          role="tooltip"
          {...popoverHoverProps}
          {...popoverTouchProps}
          class={styles.wrapper}
          style={{
            height: transitionStyles.wrapperHeight
          }}>
          <div
            style={{
              opacity: transitionStyles.popoverOpacity,
              maxHeight: transitionStyles.popoverHeight,
              transition:
                'max-height 0.1s cubic-bezier(0.0, 0.0, 0.2, 1), opacity 0.1s cubic-bezier(0.0, 0.0, 0.2, 1)'
            }}
            onTransitionEnd={handleTransitionEnd}>
            <div
              ref={popoverRef}
              class={classNames([styles.base, isDatatip ? styles[variant] : ''])}>
              {text}
            </div>
          </div>
        </div>
      </Floating>
    </Layer>
  );

  return {
    tooltipContent: !isHidden && renderContent,
    tooltipProps: actionableProps
  };
};
